import React, { Component } from "react";
import { cellValue } from "./Util";
import EditableCellShow from "./EditableCellShow";
import EditableCellPublic from "./EditableCellPublic";
import EditableCellString from "./EditableCellString";
import EditableCellSelect from "./EditableCellSelect";
import EditableCellMultiLang from "./EditableCellMultiLang";
import "./index.less";
/**
 * 
 * @param {String} type  string -  select - 
 * @param {String} value 
 * @param {Boolen} editable 
 * @param {Number} cellIndex 
 * @param {String} cellKey key
 * @param {Boolen} cellRequired 
 * @param {Function} cellChange 
 * @param {Array|Object} options  options - [{value:'value',text:''}] 
 * @param {String} cellMark 
 */
class EditableCell extends Component {
    constructor(props) {
        super(props);
        this.state = {
            cellEditable: false
        };
    }
    /**
     * 
     */
    handleCellEditableChange = editType => {
        this.setState({ cellEditable: editType });
    };
    /**
     * 
     * @param {Object} props
     */
    switchCell = props => {
        let {
            type,
            cellKey,
            cellIndex,
            value,
            cellChange,
            cellRequired,
            cellCheck,
            options,
            cellErrorMsg,
            cellMark
        } = props;
        switch (type) {
            case "string":
                return (
                    <EditableCellString
                        cellKey={cellKey}
                        cellIndex={cellIndex}
                        setCellEdit={this.handleCellEditableChange}
                        value={value}
                        cellChange={cellChange}
                        cellRequired={cellRequired}
                        cellCheck={cellCheck}
                        cellErrorMsg={cellErrorMsg}
                    />
                );
            case "select":
                return (
                    <EditableCellSelect
                        cellKey={cellKey}
                        cellIndex={cellIndex}
                        setCellEdit={this.handleCellEditableChange}
                        value={value}
                        cellChange={cellChange}
                        cellRequired={cellRequired}
                        options={options}
                        cellErrorMsg={cellErrorMsg}
                        refNode={this.refs["tableEditableCell"]}
                    />
                );
            case "multiString":
                return (
                    <EditableCellMultiLang
                        cellKey={cellKey}
                        cellIndex={cellIndex}
                        setCellEdit={this.handleCellEditableChange}
                        value={value}
                        cellChange={cellChange}
                        cellRequired={cellRequired}
                        options={options}
                        cellErrorMsg={cellErrorMsg}
                        cellMark={cellMark}
                        cellCheck={cellCheck}
                    />
                );
            default:
                break;
        }
    };
    /**
     * 
     */
    renderCell = props => {
        // 
        if (props.editable) {
            //  
            if (this.state.cellEditable) {
                return this.switchCell(props);
            } else {
                return (
                    <EditableCellPublic
                        value={cellValue(props)}
                        setCellEdit={this.handleCellEditableChange}
                    />
                );
            }
        } else {
            return <EditableCellShow value={cellValue(props)} />;
        }
    };
    componentWillReceiveProps(nextProps) {
        if (!nextProps.editable) {
            this.setState({
                cellEditable: false
            });
        }
    }
    render() {
        return (
            <div className="table-editable-cell" ref="tableEditableCell" fieldid={this.props.cellMark || this.props.cellKey}>
                {this.renderCell(this.props)}
            </div>
        );
    }
}

export default EditableCell;
