import React from "react";
import { high } from "nc-lightapp-front";
import {langCheck} from "Pub/js/utils";
const { Refer } = high;

export var conf = {
    placeholder: langCheck('0000PUB-000052'),/*  +*/
    rootNode: { refname: langCheck('0000PUB-000052'), refpk: "root" },/*  +*/
    refType: "tree",
    refName: langCheck('0000PUB-000053'),/*  +*/
    refCode: "uapbd.org.BusinessUnitAndGroupTreeRef",
    queryTreeUrl: "/nccloud/uapbd/ref/BusinessUnitAndGroupTreeRef.do",
    treeConfig: { name: [langCheck('0000PUB-000032'), langCheck('0000PUB-000033')], code: ["refcode", "refname"] },/*  ,*/
    isMultiSelectedEnabled: false,
    unitProps: {
        queryTreeUrl: "/nccloud/riart/ref/groupRefTreeAction.do",
        refType: "tree",
        //isMultiSelectedEnabled:true
        refName: langCheck('0000PUB-000054'),/*  */
        rootNode: { refname: langCheck('0000PUB-000054'), refpk: "root" }/*  */
    },
    isShowUnit: false
};

export default function(props = {}) {
    return <Refer {...Object.assign(conf, props)} />;
}
