/**
 *  
 * @param {Array} data 
 */
export function generateData(data) {
    //  tree 
    let treeArray = [];
    //  children 
    let treeObj = {};
    data.map((item, index) => {
        let { pid, code, name, appCode, pk } = item;
        if (item.children) {
            delete item.children;
        }
        item.text = `${code} ${name}`;
        item.title = item.text;
        item.key = code;
        //  parentcode  key parentcode   
        if (pid) {
            if (!treeObj[pid]) {
                treeObj[pid] = [];
            }
            treeObj[pid].push(item);
        } else {
            //   children 
            item.children = [];
            treeArray.push(item);
        }
    });
    return {
        treeArray,
        treeObj
    };
}
//
export function generateTemData(data) {
    //  tree 
    let treeArray = [];
    //  children 
    let treeObj = {};
    let allTemplateId = [];
    let allParentId = [];
    data.map((item, index) => {
        let { templateId, parentId, name, type, code } = item;
        if (item.children) {
            delete item.children;
        }
        item.key = templateId;
        item.text = code + ' ' + name;
        item.pk = templateId;

        //  parentcode  key parentcode   
        // if (parentId === 'root') {
        //     //   children 
        //     item.children = [];
        //     treeArray.push(item);
        // } else {
        //     treeObj[templateId] = [];
        //     treeObj[templateId].push(item);
        // }

        allTemplateId.push(templateId);//templateId
        allParentId.push(item);//parentId
    });
    allParentId.map((item) => {
        let { templateId, parentId, name, type, code } = item;
        if (allTemplateId.includes(item.parentId)) {
            treeObj[templateId] = [];
            treeObj[templateId].push(item);
        } else {
            item.children = [];
            treeArray.push(item);
        }
    });
    return {
        treeArray,
        treeObj
    };
}
export function generateRoData(data) {
    //  tree 
    let treeArray = [];
    data.map((item, index) => {
        let { code, id, name } = item;
        item.key = id;
        item.text = `${code} ${name}`;
        item.title = item.text;
        treeArray.push(item);
    });
    return treeArray;
}
/**
 * 
 * @param {Array} data 
 */
export function generateTreeData(data) {
    return data.map((item, index) => {
        item = Object.assign({}, item);
        if (item.children) {
            item.isLeaf = false;
            item.children = generateTreeData(item.children);
        } else {
            item.isLeaf = true;
        }
        return item;
    });
}
